import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      routeName: 'rooms',
      form: {
        type: '',
        roomNo: '',
        beds: '',
        fee: 0
      }
    }
  },

  computed: {
    ...mapGetters(['roomsPaginated'])
  },

  methods: {
    ...mapActions(['loadguestsroomsPaginated']),

    loadPaginationPage (url) {
      if (!url) { return }
      this.loadguestsroomsPaginated(url)
    }
  }
}
